* {
  font-family: 'Raleway', sans-serif;
}

body {
  background-color: #ebebeb
}

//top
.top {
  margin-top: 20px;
  margin-bottom: 10px;
}

.blue_button {
  border-radius: 6px;
  background-color: rgb(80,197,218);
  width: 166px;
  height: 30px;
  padding: 0;
  color: white;
}

#glass, #srch-term {
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 0;
  height: 30px;
}

#glass {
  margin-right: -10px;
}

#srch-term::placeholder {
  opacity: 0.5;
}

#glass img {
  margin: -8px;
  margin-top: -15px;
}


//navbar

.navbar-nav, .navbar-brand {
  margin-top: 30px;
  margin-bottom: 10px;
}

.nav-link {
  color: #5b5a5a;
}
.nav-link:hover, .dropdown-item:hover, .navbar-brand:hover {
  color: #096c7e;
}

.navbar-brand {
  font-family: Times New Roman;
  font-size: 22px;
  color: black;
}

.vl {
  border-left: 1px solid lightgrey;
  height: 25px;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 1%;
}

//main section

.arrow-down {
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid #f00;
  display: inline-block;
}